import React, { Component, Fragment } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Main from "./pages/main";

class App extends Component {
  render() {
    return (
      <Fragment>
        <Router>
          <Route path="/" exact component={Main} />
        </Router>
      </Fragment>
    );
  }
}

export default App;
